.dlf-visible {
  visibility: visible !important;
}
.dlf-shaka[data-mode="video"] {
  --controls-color: white;
  --volume-base-color: rgba(255, 255, 255, 0.54);
  --volume-level-color: rgba(255, 255, 255);
}
.dlf-shaka[data-mode="video"] .dlf-media-flat-seek-bar {
  --base-color: rgba(255, 255, 255, 0.3);
  --buffered-color: rgba(255, 255, 255, 0.54);
  --played-color: #ffffff;
}
.dlf-shaka[data-mode="audio"] {
  --controls-color: #2a2b2c;
  --volume-base-color: rgba(0, 0, 0, 0.4);
  --volume-level-color: rgba(0, 0, 0, 0.8);
}
.dlf-shaka[data-mode="audio"] .dlf-media-flat-seek-bar {
  --base-color: rgba(0, 0, 0, 0.3);
  --buffered-color: rgba(0, 0, 0, 0.54);
  --played-color: #2a2b2c;
}
.dlf-shaka[data-mode="audio"] .dlf-media-flat-seek-bar .dlf-media-chapter-marker {
  background-color: #c3e5ec;
}
.dlf-shaka .dlf-media-shaka-box {
  background-color: black;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.dlf-shaka .dlf-media-error {
  display: none;
  color: white;
  z-index: 1;
}
.dlf-shaka .dlf-media-error.dlf-visible {
  display: flex;
  justify-content: center;
  align-items: center;
}
.dlf-shaka .shaka-scrim-container {
  display: none;
}
.dlf-shaka[data-mode=""] {
  display: none;
}
.dlf-shaka .dlf-media-error {
  color: var(--controls-color);
}
.dlf-shaka .shaka-controls-button-panel > * {
  color: var(--controls-color) !important;
}
.dlf-shaka .shaka-volume-bar::-webkit-slider-thumb,
.dlf-shaka .shaka-volume-bar::-moz-range-thumb {
  background: var(--volume-level-color);
}
.dlf-shaka[data-mode="audio"] {
  height: 3.5em;
  width: 100%;
  background-color: rgba(79, 179, 199, 0.6);
}
.dlf-shaka[data-mode="audio"] video {
  display: none;
}
.dlf-shaka[data-mode="audio"] .dlf-media-poster {
  display: none !important;
}
.dlf-shaka[data-mode="audio"] .dlf-media-shaka-box {
  background-color: transparent;
}
.dlf-shaka[data-mode="audio"] .shaka-spinner-container {
  display: none;
}
.dlf-shaka[data-mode="audio"] .shaka-bottom-controls {
  width: 100%;
  padding-bottom: 0.4em;
}
.shaka-bottom-controls dlf-waveform {
  /* Transparent unless explicitly made opaque through container attributes. */
  opacity: 0;
  /* When we show/hide this, do it gradually using cubic-bezier timing. */
  transition: opacity cubic-bezier(0.4, 0, 0.6, 1) 600ms;
  /* Show controls when the container's "shown" or "casting" attributes are
   * set. */
}
.shaka-controls-container[shown="true"] .shaka-bottom-controls dlf-waveform,
.shaka-controls-container[casting="true"] .shaka-bottom-controls dlf-waveform {
  opacity: 1;
}
.dlf-media-player {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                          supported by Chrome, Edge, Opera and Firefox */
  position: relative;
}
dlf-media:not(:defined) {
  display: none;
}
dlf-media dlf-chapter {
  display: none;
}
dlf-media dlf-media-controls {
  display: none;
}
.dlf-media {
  width: 100%;
  height: 100%;
}
.dlf-media-poster {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-color: black;
}
.dlf-media-poster[src].dlf-visible {
  display: block;
}
.shaka-video-container {
  height: 100%;
}
.shaka-bottom-controls {
  visibility: hidden;
}
.shaka-controls-button-panel {
  justify-content: flex-start !important;
}
@media all and (min-width: calc(768px + 1px)) {
  .shaka-controls-button-panel button {
    margin-left: 8px;
    margin-right: 8px;
  }
  .shaka-overflow-menu-button {
    margin-left: 0 !important;
  }
  .shaka-fullscreen-button {
    margin-left: 3px !important;
    margin-right: 4px !important;
  }
}
.shaka-current-time {
  flex-shrink: 1 !important;
  white-space: nowrap;
  overflow-x: scroll;
  position: relative;
}
.shaka-current-time::-webkit-scrollbar {
  display: none;
}
body .shaka-video-container {
  touch-action: none !important;
}
body.seek-or-scrub .shaka-play-button,
body.seek-or-scrub .shaka-controls-button-panel {
  pointer-events: none;
}
body.seek-or-scrub * {
  cursor: grabbing !important;
}
.dlf-media-thumbnail-preview {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                          supported by Chrome, Edge, Opera and Firefox */
  visibility: hidden;
  position: absolute;
  bottom: 0px;
  padding-bottom: 25px;
  padding-top: 50px;
  z-index: 1;
  cursor: pointer;
  text-align: center;
}
.dlf-media-thumbnail-preview .displayed {
  display: block !important;
}
.dlf-media-thumbnail-preview .content-box {
  background-color: rgba(23, 30, 30, 0.6);
  border-radius: 4px;
  padding: 1em;
}
.dlf-media-thumbnail-preview .display {
  display: none;
  position: relative;
  width: 160px;
  height: 90px;
  border: 1px solid white;
  box-sizing: content-box;
  margin-bottom: 0.75em;
  overflow: hidden;
}
.dlf-media-thumbnail-preview .display.is-open {
  display: block;
}
.dlf-media-thumbnail-preview .display img {
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
}
.dlf-media-thumbnail-preview .info {
  display: inline-block;
  color: white;
  font-size: 14px;
  line-height: 110%;
  max-width: 160px;
}
.dlf-media-thumbnail-preview .info .chapter-text {
  line-height: 110%;
  margin-bottom: 0.4em;
  display: none;
}
.dlf-media-thumbnail-preview .info .timecode-text {
  display: block;
}
.dlf-media-thumbnail-preview .info.on-chapter-marker .chapter-text {
  font-weight: bold;
}
.dlf-media-chapter-marker {
  z-index: 0;
}
@media all and (pointer: fine) {
  .dlf-media-chapter-marker {
    width: 4px;
    height: 4px;
    background-color: #d5dfdf;
    border-radius: 2px;
  }
  .dlf-media-flat-seek-bar {
    /* Transparent unless explicitly made opaque through container attributes. */
    opacity: 0;
    /* When we show/hide this, do it gradually using cubic-bezier timing. */
    transition: opacity cubic-bezier(0.4, 0, 0.6, 1) 600ms;
    /* Show controls when the container's "shown" or "casting" attributes are
   * set. */
    -webkit-tap-highlight-color: transparent;
    touch-action: none;
    position: relative;
    margin: 0px 6px;
    padding-top: 6px;
    height: 16px;
    cursor: pointer;
  }
  .shaka-controls-container[shown="true"] .dlf-media-flat-seek-bar,
  .shaka-controls-container[casting="true"] .dlf-media-flat-seek-bar {
    opacity: 1;
  }
  .dlf-media-flat-seek-bar .range {
    position: absolute;
    width: 100%;
    height: 4px;
    border-radius: 4px;
  }
  .dlf-media-flat-seek-bar .seek-marker {
    position: absolute;
    left: 45%;
    height: 4px;
    width: 1px;
    background-color: #4e6666;
    z-index: -1;
    visibility: hidden;
  }
  .dlf-media-flat-seek-bar .seek-thumb-bar {
    position: absolute;
    left: 45%;
    height: 4px;
    width: 1px;
    background-color: rgba(78, 102, 102, 0.4);
    z-index: -2;
    visibility: hidden;
  }
}
@media all and (pointer: coarse) {
  .dlf-media-chapter-marker {
    width: 1px;
    height: 5px;
    margin-top: 1px;
    background-color: rgba(213, 223, 223, 0.3);
  }
  .dlf-media-flat-seek-bar {
    /* Transparent unless explicitly made opaque through container attributes. */
    opacity: 0;
    /* When we show/hide this, do it gradually using cubic-bezier timing. */
    transition: opacity cubic-bezier(0.4, 0, 0.6, 1) 600ms;
    /* Show controls when the container's "shown" or "casting" attributes are
   * set. */
    -webkit-tap-highlight-color: transparent;
    touch-action: none;
    position: relative;
    margin: 0px 6px;
    padding-top: 8px;
    height: 23px;
    cursor: pointer;
  }
  .shaka-controls-container[shown="true"] .dlf-media-flat-seek-bar,
  .shaka-controls-container[casting="true"] .dlf-media-flat-seek-bar {
    opacity: 1;
  }
  .dlf-media-flat-seek-bar .range {
    position: absolute;
    width: 100%;
    height: 7px;
    border-radius: 7px;
  }
  .dlf-media-flat-seek-bar .seek-marker {
    position: absolute;
    left: 45%;
    height: 7px;
    width: 1px;
    background-color: #4e6666;
    z-index: -1;
    visibility: hidden;
  }
  .dlf-media-flat-seek-bar .seek-thumb-bar {
    position: absolute;
    left: 45%;
    height: 7px;
    width: 1px;
    background-color: rgba(78, 102, 102, 0.4);
    z-index: -2;
    visibility: hidden;
  }
}
